import navStore from 'integration/stores/navStore';
import offerModalStore from 'integration/stores/offerModalStore';
import { derived } from 'svelte/store';
import querystring from 'querystring';
import { parsePrefixQuery, PrefixQueryParams } from 'utils/parsePrefixQuery';
import comboModalStore from './comboModalStore';

export function createNavQueryStore() {
	const params = querystring.parse(window.location.search.slice(1));
	const parsedParams = parsePrefixQuery(params);
	let currentState: PrefixQueryParams = parsedParams;
	const { subscribe } = derived(
		[navStore, offerModalStore, comboModalStore],
		([navState, offerModalState, comboModalState], set) => {
			if (navStore.isSync() && offerModalStore.isSync()) {
				const newState = {
					oflk__combo: comboModalState.isOpen
						? comboModalState.combo?._id || ''
						: '',
					oflk__category: navState.activeCategory?._id || '',
					oflk__company: navState.activeCompany?._id || '',
					oflk__event: navState.activeEvent?._id || '',
					oflk__offer: offerModalState.isOpen
						? offerModalState.offer?._id || ''
						: '',
				};
				if (JSON.stringify(currentState) !== JSON.stringify(newState)) {
					currentState = newState;
					set(newState);
				}
			}
		},
		currentState,
	);

	return {
		subscribe,
	};
}

export default createNavQueryStore();
