<script lang="ts">import clientStore from 'integration/stores/clientStore';
import IconSale from 'integration/lenta/icons/iconSale.svelte';
import Loader from 'integration/lenta/loader/loader.svelte';
;
import offerModalStore from 'integration/stores/offerModalStore';
import { fly } from 'svelte/transition';
import tmt from 'integration/tmt';
export let offer;
export let height;
let containerRef;
let contentRef;
let inputRef;
$: if (inputRef && containerRef) {
    const inputRect = inputRef.getBoundingClientRect();
    const containerRect = containerRef.getBoundingClientRect();
    height =
        containerRef.clientHeight -
            (containerRect.bottom - inputRect.bottom) +
            20;
    containerRef.style.height = `${height}px`;
}
let isExpand = false;
$: if (containerRef && contentRef && inputRef) {
    if (isExpand) {
        containerRef.style.height = `${contentRef.clientHeight}px`;
        inputRef.focus();
    }
    else {
        containerRef.style.height = `${height}px`;
        inputRef.blur();
    }
}
function toggleExpand() {
    isExpand = !isExpand;
}
function onExpand(is) {
    isExpand = is;
}
$: if (inputRef && contentRef) {
    inputRef.onfocus = () => onExpand(true);
    inputRef.onclick = () => onExpand(true);
}
let positionField = 'screenY';
let lastPosition = 0;
const onTouchstart = (e) => {
    const currentPosition = e.changedTouches[0][positionField];
    lastPosition = currentPosition;
};
const onTouchend = (e) => {
    const currentPosition = e.changedTouches[0][positionField];
    const delta = lastPosition - currentPosition;
    lastPosition = currentPosition;
    if (delta > 1) {
        onExpand(true);
    }
    if (delta < -1) {
        onExpand(false);
    }
};
$: clientState = $clientStore;
const handleInput = (e) => {
    const value = e.currentTarget.value;
    clientStore.setEmail(value);
};
$: isLoading = offer.promocode ? offer.promocode.loading : false;
let submitRef;
let validated = false;
const handleSubmit = (e) => {
    e.preventDefault();
    if (clientState.email) {
        offerModalStore.activate();
        tmt.clickGet(offer._id);
    }
    else {
        inputRef.focus();
        validated = true;
    }
};
$: if (validated && submitRef) {
    setTimeout(() => {
        if (submitRef) {
            submitRef.click();
        }
    }, 10);
}
const handleFocusEmail = () => {
    tmt.formEmail();
};
const handleHover = () => {
    tmt.hoverGet(offer._id);
};
function handleLink() {
    const link = document.createElement('a');
    link.setAttribute('target', '_blank');
    link.setAttribute('rel', 'noreferrer');
    link.setAttribute('href', 'https://lenta.com/pokupatelyam/politika-v-oblasti-obrabotki-personalnykh-dannykh-i-spisok-kontragentov/');
    link.click();
    // tmt.formPersonalData();
}
</script>

<div
	class="container-qVksFH"
	bind:this={containerRef}
	on:touchstart={onTouchstart}
	on:touchend={onTouchend}
	in:fly={{ y: 200, duration: 300 }}
	out:fly={{ y: -200, duration: 300 }}
>
	<div class="content-2ZTgeR" bind:this={contentRef}>
		<form on:submit={handleSubmit}>
			<div class="line-SEMLbY" />
			<div
				class="arrow-2mgn4x"
				class:down-O19LFs={isExpand}
				class:up-eBYMEm={!isExpand}
				on:click={toggleExpand}
			/>
			<div class="label-2I7K_w">Получите промокод!</div>
			<div class="desc-Tp7tde">
				Укажите ваш email, чтобы получить промокод
			</div>
			<div class="input-2T4Va_" class:input_loading-1nTXuO={isLoading}>
				{#if isLoading}
					<div class="input__loader-1mp-RO">
						<Loader size={1.5} unit="em" color="#ffc700" />
					</div>
				{/if}
				<input
					type="email"
					required={validated}
					bind:this={inputRef}
					on:input={handleInput}
					on:focus={handleFocusEmail}
					value={clientState.email}
					placeholder="Ваш email"
					disabled={isLoading}
					name="email"
				/>
			</div>
			<div class="info-2PgdVL">
				Нажимая “Получить”, вы даёте согласие на
				<span role="button" on:click={handleLink} class="link-3mg5GC">
					обработку персональных данных
				</span>
			</div>
			<button
				class="button-3pWo-5"
				type="submit"
				disabled={isLoading}
				bind:this={submitRef}
				on:mouseenter={handleHover}
			>
				Получить промокод <IconSale />
			</button>
			<form />
		</form>
	</div>
</div>

<style lang="scss">:global(.container-qVksFH) {
  max-height: calc(100vh - 60px);
  max-height: calc(var(--app-height) - 60px);
  width: 100%;
  position: fixed;
  bottom: 0;
  z-index: 100;
  height: auto;
  transition: position 0.3s, height 0.3s;
}

:global(.content-2ZTgeR) {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #f8f8f8;
  padding: 30px 20px;
  text-align: center;
  justify-content: center;
  box-shadow: 0px -10px 9px -10px rgba(0, 0, 0, 0.1), 0px -2px 39px rgba(0, 0, 0, 0.2);
  border-radius: 16px 16px 0px 0px;
}

:global(.line-SEMLbY) {
  position: absolute;
  background: #e0e0e0;
  border-radius: 50px;
  height: 4px;
  width: 64px;
  top: 10px;
  right: 50%;
  transform: translate(50%, 0);
}

:global(.arrow-2mgn4x) {
  top: 15px;
  right: 15px;
  position: absolute;
  border: solid #bdbdbd;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
  transform: translate(0, -100%);
}

:global(.up-eBYMEm) {
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
}

:global(.down-O19LFs) {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

:global(.label-2I7K_w) {
  display: flex;
  justify-content: center;
  font-weight: 700;
  font-size: 1.25em;
  margin-bottom: 0.5em;
}

:global(.desc-Tp7tde) {
  font-size: 0.875em;
  margin-bottom: 1em;
}

:global(.input-2T4Va_) {
  position: relative;
  overflow: hidden;
  padding: 2px;
  margin: -2px;
  margin-bottom: 18px;
  border-radius: 8px;
}
:global(.input-2T4Va_) input {
  height: 3em;
  width: 100%;
  outline: none;
  padding: 1em;
  background: #ffffff;
  border: 1px solid #e0e0e0;
  box-sizing: border-box;
  border-radius: 8px;
  position: relative;
}
:global(.input-2T4Va_) input:focus {
  border: 1px solid #ffc700;
}
:global(.input-2T4Va_::before) {
  content: "";
  display: block;
  position: absolute;
  top: -170px;
  left: -30px;
  width: 400px;
  height: 400px;
  border-radius: 50%;
  box-sizing: border-box;
  --angle: 0deg;
  background: white;
  background: conic-gradient(rgba(255, 255, 255, 0) 33%, #ffc700 50%, rgba(255, 255, 255, 0) 66%);
  animation: RotateAnim 3s linear infinite;
}
@keyframes RotateAnim {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
:global(.input-2T4Va_.input_loading-1nTXuO) input {
  padding-right: 3em;
}
:global(.input-2T4Va_) :global(.input__loader-1mp-RO) {
  position: absolute;
  top: 0.75em;
  right: 1em;
}

:global(.info-2PgdVL) {
  font-size: 0.75em;
  font-weight: 400;
  color: #979797;
  margin-bottom: 20px;
}

:global(.link-3mg5GC) {
  text-decoration: underline;
  cursor: pointer;
}

:global(.button-3pWo-5) {
  cursor: pointer;
  outline: none;
  text-align: center;
  white-space: nowrap;
  padding: 1em 1.5em;
  background: linear-gradient(135deg, #fadc4b 0%, #ffc700 100%);
  box-shadow: 0px 13px 18px -14px rgba(0, 0, 0, 0.2);
  border: 0;
  border-radius: 0.5em;
  box-sizing: content-box;
  font-weight: 500;
}
:global(.button-3pWo-5) :global(svg) {
  vertical-align: middle;
}
:global(.button-3pWo-5:hover) {
  transition: transform 0.05s;
  transform: translate(0, -2px);
}</style>
