import rootMessageEmitter from 'integration/rootMessageEmmiter';
import { initialFeedbackPartnerModal } from 'store/reducers/feedbackPartnerModal';
import { writable } from 'svelte/store';
import type { FeedbackPartnerModalState } from 'types/state';
import type RootMessageEmitter from 'utils/messageEmitter/RootMessageEmitter';
import type { AppEmmiters } from 'utils/messageEmitter/types';

export function createFeedbackPartnerModalStore(
	messageEmitter: RootMessageEmitter,
) {
	let isSync = false;
	const { subscribe, update } = writable<FeedbackPartnerModalState>(
		initialFeedbackPartnerModal,
		function start() {
			const onSetState = (
				payload: AppEmmiters['app:feedback-partner-modal/setState'],
			) => {
				isSync = true;
				update(() => payload);
			};
			const onUpdate = (
				payload: AppEmmiters['app:feedback-partner-modal/update'],
			) => {
				update((prev) => ({ ...prev, ...payload }));
			};
			messageEmitter.addListener(
				'app:feedback-partner-modal/setState',
				onSetState,
			);
			messageEmitter.addListener(
				'app:feedback-partner-modal/update',
				onUpdate,
			);
			messageEmitter.emit('root:feedback-partner-modal/getState');
			return function stop() {
				messageEmitter.removeListener(
					'app:feedback-partner-modal/setState',
					onSetState,
				);
				messageEmitter.removeListener(
					'app:feedback-partner-modal/update',
					onUpdate,
				);
			};
		},
	);

	return {
		isSync: () => isSync,
		subscribe,
		open: () => {
			messageEmitter.emit('root:feedback-partner-modal/update', {
				isOpen: true,
			});
		},
		close: () => {
			messageEmitter.emit('root:feedback-partner-modal/update', {
				isOpen: false,
			});
		},
		setMessage: (message: string) => {
			messageEmitter.emit('root:feedback-partner-modal/update', {
				message,
			});
		},
		submit: () => {
			messageEmitter.emit('root:feedback-partner-modal/submit');
		},
	};
}

export default createFeedbackPartnerModalStore(rootMessageEmitter);
