import rootMessageEmitter from 'integration/rootMessageEmmiter';
import { initialOfferModalState } from 'store/reducers/offerModal';
import { writable } from 'svelte/store';
import type { OfferModalState } from 'types/state';
import type RootMessageEmitter from 'utils/messageEmitter/RootMessageEmitter';
import type { AppEmmiters } from 'utils/messageEmitter/types';

export function createOfferModalStore(messageEmitter: RootMessageEmitter) {
	let isSync = false;
	const { subscribe, update } = writable<OfferModalState>(
		initialOfferModalState,
		function start() {
			const onOpen = (payload: AppEmmiters['app:offer-modal/open']) => {
				update((prev) => ({
					...prev,
					isOpen: true,
					...payload,
				}));
			};

			const onClose = () => {
				update((prev) => ({
					...prev,
					isOpen: false,
				}));
			};

			const onSetState = (
				payload: AppEmmiters['app:offer-modal/setState'],
			) => {
				isSync = true;
				update(() => payload);
			};

			const onUpdate = (
				payload: AppEmmiters['app:offer-modal/update'],
			) => {
				update((prev) => ({ ...prev, ...payload }));
			};

			messageEmitter.addListener('app:offer-modal/open', onOpen);
			messageEmitter.addListener('app:offer-modal/close', onClose);
			messageEmitter.addListener('app:offer-modal/update', onUpdate);
			messageEmitter.addListener('app:offer-modal/setState', onSetState);

			messageEmitter.emit('root:offer-modal/getState');
			return function stop() {
				messageEmitter.removeListener('app:offer-modal/open', onOpen);
				messageEmitter.removeListener('app:offer-modal/close', onClose);
				messageEmitter.removeListener(
					'app:offer-modal/update',
					onUpdate,
				);
				messageEmitter.removeListener(
					'app:offer-modal/setState',
					onSetState,
				);
			};
		},
	);

	return {
		isSync: () => isSync,
		subscribe,
		open: (offerID: string) => {
			messageEmitter.emit('root:offer-modal/open', { offerID });
		},
		close: () => {
			messageEmitter.emit('root:offer-modal/close');
		},
		activate: () => {
			messageEmitter.emit('root:offer-modal/activate');
		},
	};
}

export default createOfferModalStore(rootMessageEmitter);
