import { useSelector } from 'react-redux';
import { EventState, State } from 'types/state';
import generateHashName from 'utils/generateHashName';
import { createSelector } from 'reselect';

export const EVENT_CLASSES = {
	//** Класс карточки оффера */
	card: 'event__card',
	//** Класс для кнопки в карточке оффера */
	card_button: 'event__card_button',
	//** Класс для кнопки в карточке оффера при наведении */
	card_button_hover: 'event__card_button_hover',
	//** Класс для бейджа с название эвента*/
	card_badge: 'event__card_badge',
};

function getHashClasses(eventStyles = '') {
	const classes: Record<string, string> = {};

	let regStr = '';

	const classNames: Record<string, string> = {};
	for (const key in EVENT_CLASSES) {
		const className = (EVENT_CLASSES as Record<string, string>)[key];
		const hashedClass = `oflk-${className}-${generateHashName(className)}`;
		classNames[className] = hashedClass;
		classes[key] = hashedClass;
		regStr += `(${className}\\b)|`;
	}

	regStr = regStr.slice(0, regStr.length - 1);
	const regExp = new RegExp(regStr, 'g');

	const style = eventStyles.replace(regExp, (match) => {
		if (match) {
			return classNames[match];
		}
		return match;
	});

	return {
		classes,
		style,
	};
}

export const eventSelector = createSelector<State, State['event'], EventState>(
	(state) => state.event,
	(event) => {
		const { classes, style } = getHashClasses(event?.style);
		if (event) {
			event = {
				...event,
				style,
			};
		}
		return {
			classes,
			event,
		};
	},
);

export default function useEvent() {
	return useSelector(eventSelector);
}
