import getFrameApp from './getFrameApp';
import rootMessageEmitter from './rootMessageEmmiter';

export default function getFramePath() {
	try {
		const iframe = getFrameApp();
		if (iframe.src) {
			const url = new URL(iframe.src);
			return `${url.origin}/overlay`;
		} else {
			return rootMessageEmitter.targetOrigin;
		}
	} catch (error) {
		return rootMessageEmitter.targetOrigin;
	}
}
