import { EVENT_CLASSES } from 'hooks/state/useEvent';
import rootMessageEmitter from 'integration/rootMessageEmmiter';
import { writable } from 'svelte/store';
import { EventState } from 'types/state';
import type RootMessageEmitter from 'utils/messageEmitter/RootMessageEmitter';
import type { AppEmmiters } from 'utils/messageEmitter/types';

export function createEventStore(messageEmitter: RootMessageEmitter) {
	let isSync = false;
	const { subscribe, update } = writable<EventState>(
		{
			event: null,
			classes: EVENT_CLASSES,
		},
		function start() {
			const onSetState = (payload: AppEmmiters['app:event/setState']) => {
				isSync = true;
				update(() => payload);
			};

			messageEmitter.addListener('app:event/setState', onSetState);

			messageEmitter.emit('root:event/getState');
			return function stop() {
				messageEmitter.removeListener('app:event/setState', onSetState);
			};
		},
	);

	return {
		isSync: () => isSync,
		subscribe,
	};
}

export default createEventStore(rootMessageEmitter);
