import navStore from 'integration/stores/navStore';
import offerModalStore from 'integration/stores/offerModalStore';
import { derived } from 'svelte/store';
import clientStore from './clientStore';
import eventStore from './eventStore';
import feedbackClientModalStore from './feedbackClientModalStore';
import feedbackPartnerModalStore from './feedbackPartnerModalStore';

export function createIsSyncStore() {
	const initState = {
		isSync: false,
	};
	const { subscribe } = derived(
		[
			navStore,
			offerModalStore,
			clientStore,
			eventStore,
			feedbackClientModalStore,
			feedbackPartnerModalStore,
		],
		(_derivedState, set) => {
			if (
				navStore.isSync() &&
				offerModalStore.isSync() &&
				clientStore.isSync() &&
				eventStore.isSync() &&
				feedbackClientModalStore.isSync() &&
				feedbackPartnerModalStore.isSync()
			) {
				set({
					isSync: true,
				});
			}
		},
		initState,
	);

	return {
		subscribe,
	};
}

export default createIsSyncStore();
