import formatDurationFns from 'date-fns/formatDuration';
import locale from 'date-fns/locale/ru';

type FormatParameters = Parameters<typeof formatDurationFns>;

function formatDuration(
	duration: FormatParameters[0],
	options?: FormatParameters[1],
): string {
	const result = formatDurationFns(duration, {
		locale,
		...options,
	});
	return result;
}

export default formatDuration;
