import { createReducer } from '@reduxjs/toolkit';
import actions from 'store/actions';
import { State } from 'types/state';

export const initialComboModalState: State['comboModal'] = {
	isOpen: false,
};

const reducer = createReducer(initialComboModalState, (builder) =>
	builder.addCase(actions.updateComboModalState, (state, { payload }) => {
		return {
			...state,
			...payload,
		};
	}),
);

export default reducer;
