<script lang="ts">import clientStore from 'integration/stores/clientStore';
import Responsive from 'integration/common/responsive/responsive.svelte';
import IconSale from 'integration/lenta/icons/iconSale.svelte';
import Loader from 'integration/lenta/loader/loader.svelte';
;
import offerModalStore from 'integration/stores/offerModalStore';
import getFramePath from 'integration/getFramePath';
import tmt from 'integration/tmt';
export let offer;
let inputRef;
let submitRef;
$: clientState = $clientStore;
const handleInput = (e) => {
    const value = e.currentTarget.value;
    clientStore.setEmail(value);
};
$: isLoading = offer.promocode ? offer.promocode.loading : false;
let validated = false;
const handleSubmit = (e) => {
    e.preventDefault();
    if (clientState.email) {
        offerModalStore.activate();
        tmt.clickGet(offer._id);
    }
    else {
        inputRef.focus();
        validated = true;
    }
};
$: if (validated && submitRef) {
    setTimeout(() => {
        if (submitRef) {
            submitRef.click();
        }
    }, 10);
}
const handleFocusEmail = () => {
    tmt.formEmail();
};
const handleHover = () => {
    tmt.hoverGet(offer._id);
};
function handleLink() {
    const link = document.createElement('a');
    link.setAttribute('target', '_blank');
    link.setAttribute('rel', 'noreferrer');
    link.setAttribute('href', 'https://lenta.com/pokupatelyam/politika-v-oblasti-obrabotki-personalnykh-dannykh-i-spisok-kontragentov/');
    link.click();
    // tmt.formPersonalData();
}
const basePath = getFramePath();
</script>

<form on:submit={handleSubmit}>
	<div class="container-244wnD">
		<div class="image-3XLg1B">
			<Responsive size="2x1">
				<img alt="" src={`${basePath}/lenta/images/sale.png`} />
			</Responsive>
		</div>
		<div class="label-2uTPco">Получите промокод!</div>
		<div class="desc-2z07x_">
			Укажите ваш email, чтобы <br /> получить промокод
		</div>
		<div class="input-gacCgd" class:input_loading-8vLr-Q={isLoading}>
			{#if isLoading}
				<div class="input__loader-1XQz5m">
					<Loader size={1.5} unit="em" color="#ffc700" />
				</div>
			{/if}
			<input
				type="email"
				required={validated}
				bind:this={inputRef}
				on:input={handleInput}
				on:focus={handleFocusEmail}
				value={clientState.email}
				placeholder="Ваш email"
				disabled={isLoading}
				name="email"
			/>
		</div>
		<div class="info-D_v4PF">
			Нажимая “Получить”, вы даёте согласие <br />
			на
			<span role="button" on:click={handleLink} class="link-VAw3ih">
				обработку персональных данных
			</span>
		</div>
		<button
			class="button-3jjMNm"
			type="submit"
			disabled={isLoading}
			bind:this={submitRef}
			on:mouseenter={handleHover}
		>
			Получить промокод <IconSale />
		</button>
	</div>
</form>

