  display: flex;
  flex-direction: column;
  background-color: #f8f8f8;
  padding: 40px;
  text-align: center;
  height: 100%;
  justify-content: center;
}

:global(.image-3XLg1B) {
  align-self: center;
  width: 70%;
  margin-bottom: 25px;
}
:global(.image-3XLg1B) img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

:global(.label-2uTPco) {
  display: flex;
  justify-content: center;
  font-weight: 700;
  font-size: 1.25em;
  margin-bottom: 0.5em;
}

:global(.desc-2z07x_) {
  font-size: 0.875em;
  margin-bottom: 1em;
}

:global(.input-gacCgd) {
  position: relative;
}
:global(.input-gacCgd) input {
  height: 3em;
  width: 100%;
  outline: none;
  padding: 1em;
  margin-bottom: 10px;
  background: #ffffff;
  border: 1px solid #e0e0e0;
  box-sizing: border-box;
  border-radius: 8px;
}
:global(.input-gacCgd) input:focus {
  border: 1px solid #ffc700;
}
:global(.input-gacCgd) input:invalid {
  border: 1px solid #ff3636;
}
:global(.input-gacCgd.input_loading-8vLr-Q) input {
  padding-right: 3em;
}
:global(.input-gacCgd) :global(.input__loader-1XQz5m) {
  position: absolute;
  top: 0.75em;
  right: 1em;
}

:global(.info-D_v4PF) {
  font-size: 0.75em;
  font-weight: 400;
  color: #979797;
  margin-bottom: 20px;
}

:global(.link-VAw3ih) {
  text-decoration: underline;
  cursor: pointer;
}

:global(.button-3jjMNm) {
  cursor: pointer;
  outline: none;
  text-align: center;
  white-space: nowrap;
  padding: 1em 1.5em;
  background: linear-gradient(135deg, #fadc4b 0%, #ffc700 100%);
  box-shadow: 0px 13px 18px -14px rgba(0, 0, 0, 0.2);
  border: 0;
  border-radius: 0.5em;
  box-sizing: content-box;
  font-weight: 500;
}
:global(.button-3jjMNm) :global(svg) {
  vertical-align: middle;
}
:global(.button-3jjMNm:hover) {
  transition: transform 0.05s;
  transform: translate(0, -2px);
}</style>
