import type { ParsedUrlQuery } from 'querystring';
import querystring from 'querystring';

export interface PrefixQueryParams extends Record<string, string | undefined> {
	oflk__category: string;
	oflk__company: string;
	oflk__event: string;
	oflk__offer: string;
	oflk__combo: string;
}

export function parsePrefixQuery(params: ParsedUrlQuery) {
	const prefixQuery: PrefixQueryParams = {
		oflk__category: '',
		oflk__event: '',
		oflk__company: '',
		oflk__offer: '',
		oflk__combo: '',
	};
	for (const key in params) {
		let param = params[key];
		if (Array.isArray(param)) {
			param = param[0];
		}
		if (param) {
			prefixQuery[key] = param;
		}
	}
	return prefixQuery;
}

export function stringifyPrefixQuery(params: PrefixQueryParams): string {
	const {
		oflk__category,
		oflk__company,
		oflk__event,
		oflk__offer,
		oflk__combo,
		...queryParams
	} = params;

	if (oflk__category) {
		queryParams.oflk__category = oflk__category;
	}

	if (oflk__company) {
		queryParams.oflk__company = oflk__company;
	}

	if (oflk__event) {
		queryParams.oflk__event = oflk__event;
	}

	if (oflk__offer) {
		queryParams.oflk__offer = oflk__offer;
	}

	if (oflk__combo) {
		queryParams.oflk__combo = oflk__combo;
	}

	return querystring.stringify(queryParams);
}
