<script lang="ts">import Modal from 'integration/lenta/modal/modal.svelte';
import IconMail from 'integration/lenta/icons/iconMail.svelte';
import IconArrowRight from 'integration/lenta/icons/iconArrowRight.svelte';
import { fade } from 'svelte/transition';
import Phone from '../phone';
import feedbackPartnerModalStore from 'integration/stores/feedbackPartnerModalStore';
import clientStore from 'integration/stores/clientStore';
import Loader from '../loader/loader.svelte';
import tmt from 'integration/tmt';
$: clientState = $clientStore;
$: modalState = $feedbackPartnerModalStore;
$: complete = false;
$: sended = false;
$: validated = false;
$: if (modalState.isLoading) {
    sended = true;
    validated = false;
}
$: if (sended && !modalState.isLoading) {
    complete = true;
    sended = false;
    validated = false;
}
$: if (!modalState.isOpen) {
    complete = false;
}
function handleClose() {
    feedbackPartnerModalStore.close();
}
const handleSubmit = (e) => {
    e.preventDefault();
    validated = true;
    if (modalState.message) {
        feedbackPartnerModalStore.submit();
        tmt.formSubmit('feedbackPartner');
    }
};
const handleMessage = (e) => {
    const value = e.currentTarget.value;
    feedbackPartnerModalStore.setMessage(value);
};
const handleEmail = (e) => {
    const value = e.currentTarget.value;
    clientStore.setEmail(value);
};
const handleFocusEmail = () => {
    tmt.formEmail();
};
</script>

<Modal isOpen={modalState.isOpen} onClose={handleClose}>
	{#if complete}
		<div
			class="container-3C7bce feedback-1fLou3"
			in:fade={{ duration: 300 }}
		>
			<div class="feedback__title-3ShdU5">
				Спасибо!<br />Сообщение отправлено
			</div>
			<div class="feedback__description-2Jan3S">
				Мы уже получили ваше сообщение, и совсем скоро наши специалисты
				свяжутся с вами
			</div>
			<button class="feedback__continue-10viEu" on:click={handleClose}>
				Продолжить <IconArrowRight />
			</button>
		</div>
	{:else}
		<div class="container-3C7bce feedback-1fLou3">
			<div class="feedback__title-3ShdU5">
				Узнайте больше о всех преимуществах партнёрства с нами
			</div>
			<div class="feedback__description-2Jan3S">Позвоните нам</div>
			<div class="feedback__phone-1v_qwc"><Phone /></div>
			<div class="feedback__description-2Jan3S">Или напишите</div>
			<form on:submit={handleSubmit} class="feedback__input-1Y8zfq">
				{#if modalState.isLoading}
					<div class="loader-1gQspl">
						<Loader size={1.5} unit="em" color="#ffc700" />
					</div>
				{/if}
				<input
					on:change={handleEmail}
					on:focus={handleFocusEmail}
					type="email"
					placeholder="Ваш email"
					value={clientState.email}
					disabled={modalState.isLoading}
				/>
				<textarea
					on:change={handleMessage}
					value={modalState.message}
					type="email"
					style={'resize: none;'}
					placeholder="Ваше сообщение"
					disabled={modalState.isLoading}
					required={validated}
				/>
				<button
					class="feedback__button-1d1dHX"
					type="submit"
					disabled={modalState.isLoading}
				>
					Отправить <IconMail />
				</button>
				<div class="feedback__info-13xqyU">
					Нажимая “Отправить”, вы даёте согласие <br />на
					<span
						role="button"
						on:click={() => {
							const link = document.createElement('a');
							link.setAttribute('target', '_blank');
							link.setAttribute('rel', 'noreferrer');
							link.setAttribute(
								'href',
								'https://lenta.com/pokupatelyam/politika-v-oblasti-obrabotki-personalnykh-dannykh-i-spisok-kontragentov/',
							);
							link.click();
						}}
						class="feedback__link-1ueCP8"
					>
						обработку персональных данных
					</span>
				</div>
			</form>
		</div>
	{/if}
</Modal>

