<script lang="ts">import OfferModal from './offerModal';
import Nav from './nav';
import EventStyle from 'integration/common/eventStyle/eventStyle.svelte';
import eventStore from 'integration/stores/eventStore';
import FeedbackClientModal from './feedbackClientModal';
import FeedbackPartnerModal from './feedbackPartnerModal';
import History from 'integration/common/history';
import generateHashName from 'utils/generateHashName';
import { onDestroy, onMount } from 'svelte';
import isSyncStore from 'integration/stores/isSyncStore';
let namespaceId = '';
const unsubscribe = eventStore.subscribe(({ event }) => {
    if (event) {
        namespaceId = generateHashName(event._id);
    }
});
$: isSync = $isSyncStore.isSync;
let style = '';
function onResize() {
    style = `--app-height: ${window.innerHeight}px; --app-width: ${window.innerWidth}px;`;
}
onResize();
onMount(() => {
    window.addEventListener('resize', onResize);
});
onDestroy(() => {
    window.removeEventListener('resize', onResize);
    unsubscribe();
});
</script>

{#if isSync}
	<div id={namespaceId} class="oflk-SlNB_H" {style}>
		<Nav />
		<OfferModal />
		<FeedbackClientModal />
		<FeedbackPartnerModal />
	</div>
	<EventStyle {namespaceId} />
	<History />
{/if}

<style lang="scss">:global(.oflk-SlNB_H) {
  font-family: Roboto, system-ui, -apple-system, "Segoe UI", "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  box-sizing: border-box;
}
:global(.oflk-SlNB_H) :global(*) {
  font-family: Roboto, system-ui, -apple-system, "Segoe UI", "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  box-sizing: border-box;
  outline: none;
}
:global(.oflk-SlNB_H) :global(*::before),
:global(.oflk-SlNB_H) :global(*::after) {
  box-sizing: border-box;
}
:global(.oflk-SlNB_H) :global(button) {
  cursor: pointer;
}</style>
