let frameApp: HTMLIFrameElement | undefined;

const getFrameApp = () => {
	if (!frameApp) {
		updateFrameApp();
	}
	return frameApp as HTMLIFrameElement;
};

export const updateFrameApp = () => {
	frameApp = undefined;
	const iframes = document.getElementsByTagName('iframe');
	for (let index = 0; index < iframes.length; index++) {
		const name = iframes[index].name;
		if (name === 'offerlake') {
			frameApp = iframes[index];
		}
	}
	if (!frameApp) {
		throw new Error('Not found offerlake iframe');
	}
};

export default getFrameApp;
