<script lang="ts">;
import eventStore from 'integration/stores/eventStore';
import offerModalStore from 'integration/stores/offerModalStore';
import OfferCardHeader from './offerCardHeader.svelte';
import { textToHtml } from 'utils/convertHtml';
import IconSale from '../icons/iconSale.svelte';
import tmt from 'integration/tmt';
export let offer;
$: eventState = $eventStore;
$: isEvent = eventState.event && offer.event === eventState.event._id;
function handleButton() {
    offerModalStore.open(offer._id);
    tmt.clickGetPromo(offer._id);
}
const handleHover = () => {
    tmt.hoverGetPromo(offer._id);
};
