import rootMessageEmitter from 'integration/rootMessageEmmiter';
import { TmtState } from 'types/state';
import type RootMessageEmitter from 'utils/messageEmitter/RootMessageEmitter';
import type { AppEmmiters } from 'utils/messageEmitter/types';

type FormTypes = 'feedbackPartner';

class Tmt {
	state?: TmtState;

	constructor(private messageEmmiter: RootMessageEmitter) {}

	private setState = (state: TmtState) => {
		this.state = state;
	};

	private tmtEvent = (tmtEvent: AppEmmiters['app:tmt/event']) => {
		this.send(
			tmtEvent.eventType,
			tmtEvent.eventValue,
			tmtEvent.eventDataObject,
		);
	};

	/**
	 * @remarks метод инициализации отслеживания действий пользователя
	 * @returns метод отключения отслеживания действий пользователя
	 */
	init = () => {
		this.dispose();
		this.messageEmmiter.addListener('app:tmt/setState', this.setState);
		this.messageEmmiter.addListener('app:tmt/event', this.tmtEvent);
		this.messageEmmiter.emit('root:tmt/getState');
		return this.dispose;
	};

	/**
	 * @remarks метод отключения отслеживания действий пользователя
	 * идентичен возвращаемому значению мз метода init
	 */
	dispose = () => {
		this.state = undefined;
		this.messageEmmiter.removeListener('app:tmt/setState', this.setState);
		this.messageEmmiter.removeListener('app:tmt/event', this.tmtEvent);
	};

	clickGet = (offerId: string) => {
		this.send('clickGet', offerId);
	};

	hoverGet = (offerId: string) => {
		this.send('hoverGet', offerId);
	};

	clickGetPromo = (offerId: string) => {
		this.send('clickGetPromo', offerId);
	};

	hoverGetPromo = (offerId: string) => {
		this.send('hoverGetPromo', offerId);
	};

	formEmail = () => {
		this.send('formEmail');
	};

	formPersonalData = () => {
		this.send('formPersonalData');
	};

	formSubmit = (form: FormTypes) => {
		this.send('formSubmit', form);
	};

	send = (
		type: keyof Omit<TmtState, 'label'>,
		value: string | null = null,
		payload: Record<string, any> | null = null,
	) => {
		if (this.state && this.state[type]) {
			try {
				window.$tmt(
					'send',
					type,
					value,
					this.state.label,
					'offerlake',
					payload,
				);
			} catch (error) {
				if (process.env.NODE_ENV === 'development') {
					console.warn(`${error?.message} 
${JSON.stringify(
	{
		type,
		value,
		payload,
	},
	null,
	2,
)}`);
				}
				return;
			}
		}
	};
}

export default new Tmt(rootMessageEmitter);
