<script lang="ts">import Modal from 'integration/lenta/modal/modal.svelte';
import OfferModalInfo from '../offerModalInfo.svelte';
import OfferModalCompany from '../OfferModalCompany.svelte';
import OfferModalSimilar from '../offerModalSimilar.svelte';
import DisplayPromo from './displayPromo.svelte';
import InputPromo from './inputPromo.svelte';
import offerModalStore from 'integration/stores/offerModalStore';
import { fly } from 'svelte/transition';
$: modalState = $offerModalStore;
$: promocodeState = modalState.offer?.promocode;
$: offer = modalState.offer;
let offerRef;
let modalRef;
let companyRef;
let similarRef;
let initialHeight;
$: if (offerRef?.parentElement) {
    if (!modalRef) {
        modalRef = offerRef.parentElement;
        initialHeight = modalRef.clientHeight;
        modalRef.style.height = `${initialHeight}px`;
    }
}
$: if (!modalState.isOpen) {
    modalRef = null;
}
$: isSimilar = offer?.similar.length;
</script>

<Modal isOpen={modalState.isOpen} onClose={offerModalStore.close} scroll>
	{#if offer}
		<div class="container-HgANPj offer-2S6Kzu" bind:this={offerRef}>
			<div class="offer__info-3MLa50">
				<OfferModalInfo {offer} />
			</div>
			<div class="offer__promo-2fp6M2">
				{#if promocodeState?.payload}
					<DisplayPromo promocode={promocodeState.payload} />
				{:else}
					<InputPromo {offer} />
				{/if}
			</div>
		</div>
		<div
			bind:this={companyRef}
			transition:fly={{ y: 200, duration: 300 }}
			class="container-HgANPj company-3fYa93"
			style={isSimilar
				? 'opacity:0; position: absolute; z-index: -100;'
				: 'opacity:1;'}
		>
			<OfferModalCompany company={offer.company} />
		</div>
		{#if isSimilar}
			<div
				class="container-HgANPj similar-5saHM"
				transition:fly={{ y: -200, duration: 300 }}
				bind:this={similarRef}
				on:introstart={(e) => {
					if (modalRef) {
						const node = e.currentTarget;
						modalRef.style.transition = `height 0.7s`;
						let compressHeight =
							node.clientHeight - companyRef.clientHeight;
						if (compressHeight < 0) compressHeight = 0;
						modalRef.style.height = `${
							modalRef.clientHeight + compressHeight
						}px`;
					}
				}}
				on:introend={() => {
					if (modalRef) {
						modalRef.style.transition = ``;
						modalRef.style.height = `auto`;
					}
				}}
				on:outrostart={(e) => {
					if (modalRef) {
						const node = e.currentTarget;
						modalRef.style.transition = `height 0.7s`;
						let compressHeight =
							companyRef.clientHeight - node.clientHeight;

						modalRef.style.height = `${
							modalRef.clientHeight + compressHeight
						}px`;
					}
				}}
			>
				<OfferModalSimilar offers={offer?.similar} />
			</div>
		{/if}
	{/if}
</Modal>

<style lang="scss">:global(.container-HgANPj) {
  background-color: #fff;
  border: 1px solid #eeeeee;
  box-shadow: 0px 26px 21px -22px rgba(0, 0, 0, 0.2), 0px 20px 33px rgba(0, 0, 0, 0.05);
  border-radius: 1rem;
  z-index: 2;
}

:global(.offer-2S6Kzu) {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  margin-bottom: 20px;
}

:global(.offer__info-3MLa50) {
  display: flex;
  flex-direction: column;
  flex-basis: 60%;
}

:global(.offer__promo-2fp6M2) {
  border-top-right-radius: 1rem;
  border-bottom-right-radius: 1rem;
  overflow: hidden;
  flex-basis: 40%;
  background-color: #f8f8f8;
}

:global(.similar-5saHM) {
  z-index: 1;
  margin-top: -40px;
  padding-top: 20px;
}

:global(.company-3fYa93) {
  opacity: 1;
  transition: opacity 0.7s;
}</style>
