<script lang="ts">import NavMenu from './navMenu.svelte';
import NavStickyItem from './navStickyItem.svelte';
import navStore from 'integration/stores/navStore';
import getFrameApp from 'integration/getFrameApp';
import NavCompanies from './navCompanies.svelte';
let stickyStyle = '';
$: navState = $navStore;
$: if (navState.isSticky) {
    const iframeRect = getFrameApp().getBoundingClientRect();
    stickyStyle = `
				top: 60px;
				left: ${iframeRect.left + navState.navRect.left}px;
				width: ${navState.navRect.width}px
			`;
}
else {
    stickyStyle = '';
}
function handleActiveKey(event) {
    navStore.setActiveKey(event.detail);
}
</script>

<div
	class="nav-O3M4Ht "
	class:nav_visible-3uWS0G={!!stickyStyle}
	class:nav_invisible-2pZWI4={!stickyStyle}
	style={stickyStyle}
>
	<div class="scroll-container-1Vq48E">
		<div class="scroll-3kGl_L">
			{#each navState.mainCategories as { name, eventKey }}
				<NavStickyItem
					{name}
					{eventKey}
					active={navState.activeKey === eventKey}
					on:activeKey={handleActiveKey}
				/>
			{/each}
			<NavStickyItem
				eventKey={'expand'}
				name={'Ещё'}
				active={false}
				on:activeKey={handleActiveKey}
			/>
		</div>
	</div>
</div>

<NavMenu />
<NavCompanies />

<style lang="scss">@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap");
