  position: fixed;
  top: 0;
  opacity: 0;
  z-index: 100;
  transition: 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
}

:global(.scroll-3kGl_L) {
  cursor: initial;
  display: inline-flex;
  min-width: 100%;
  justify-content: space-between;
}

:global(.scroll-container-1Vq48E) {
  width: 100%;
  display: flex;
  overflow-x: auto;
  overflow-y: visible;
  padding-bottom: 2rem;
  margin-bottom: -1rem;
}

:global(.nav_invisible-2pZWI4) {
  opacity: 0;
  transform: translate(0, -100%);
