<script lang="ts">import { onDestroy } from 'svelte';
import querystring from 'querystring';
import navQueryStore from 'integration/stores/navQueryStore';
import { parsePrefixQuery, stringifyPrefixQuery, } from 'utils/parsePrefixQuery';
const unsubscribe = navQueryStore.subscribe((queryParams) => {
    const history = window.history;
    const location = window.location;
    const params = querystring.parse(location.search.slice(1));
    const parsedParams = parsePrefixQuery(params);
    const query = stringifyPrefixQuery({
        ...parsedParams,
        ...queryParams,
    });
    const search = query ? `?${query}` : '';
    const url = location.pathname + search + location.hash;
    history.replaceState(window.history.state, '', url);
});
onDestroy(() => {
    unsubscribe();
});
</script>
