<script lang="ts">import Responsive from 'integration/common/responsive';
import offerTime from 'integration/stores/offerTime';
import IconClock from '../icons/iconClock.svelte';
import IconFire from '../icons/iconFire.svelte';
import OfferCardSale from './offerCardSale.svelte';
;
import eventStore from 'integration/stores/eventStore';
export let onlyBadge = false;
export let offer;
// $: company = offer.company;
$: time = offerTime(offer.end);
$: eventState = $eventStore;
$: isHot = $time.duration.days <= 2;
$: isEvent = eventState.event && offer.event === eventState.event._id;
</script>

<Responsive size="16x9">
	<!-- {#if company.logoSmall}
		<img class="logo-1-I35y" alt={company.name} src={company.logoSmall} />
	{/if} -->
	<img class="image-18TCVv" alt={offer.label} src={offer.image} />
	<div class="badges-dx6YaX">
		{#if isEvent && eventState.event && !onlyBadge}
			<div class={`badge-14CSYH ${eventState.classes.card_badge}`}>
				{eventState.event.name}
			</div>
		{/if}
		{#if $time.duration.days <= 99 && $time.parsedTime}
			<div class="badge-14CSYH" class:badge_hot={isHot}>
				{#if isHot}
					<IconFire />
				{:else}
					<IconClock />
				{/if}
				{$time.parsedTime}
			</div>
		{/if}
	</div>
</Responsive>
<OfferCardSale {onlyBadge} {offer} />

