import differenceInDays from 'date-fns/differenceInDays';
import differenceInHours from 'date-fns/differenceInHours';
import differenceInMinutes from 'date-fns/differenceInMinutes';
import formatDuration from './formatDuration';

const defaultDuration = { days: 0, hours: 0, minutes: 0 };

export default function parseTime(time: number) {
	if (time === -1) {
		return {
			parsedTime: '',
			duration: defaultDuration,
		};
	}
	const interval = {
		start: new Date(),
		end: new Date(time),
	};
	const days = differenceInDays(interval.end, interval.start);
	const hoursEndDate = new Date(
		new Date(interval.end).setDate(interval.end.getDate() - days),
	);

	const hours =
		days > 0 ? 0 : differenceInHours(hoursEndDate, interval.start);

	const minutesEndDate = new Date(
		new Date(hoursEndDate).setHours(hoursEndDate.getHours() - hours),
	);

	const minutes =
		hours > 0 || days > 0
			? 0
			: differenceInMinutes(minutesEndDate, interval.start);

	const duration = { days, hours, minutes };

	if (days < 0 || hours < 0 || minutes < 0) {
		return {
			parsedTime: '',
			duration,
		};
	}
	return {
		duration,
		parsedTime: formatDuration(duration),
	};
}
