import { createReducer } from '@reduxjs/toolkit';
import actions from 'store/actions';
import { State } from 'types/state';

export const initialFeedbackPartnerModal: State['feedbackPartnerModal'] = {
	isOpen: false,
	error: null,
	isLoading: false,
	message: '',
};

const reducer = createReducer(initialFeedbackPartnerModal, (builder) =>
	builder
		.addCase(actions.updateFeedbackPartnerModal, (state, { payload }) => {
			return {
				...state,
				...payload,
			};
		})
		.addCase(actions.submitFeedbackPartner, (state) => {
			return {
				...state,
				error: null,
				isLoading: true,
			};
		})
		.addCase(actions.submitFeedbackPartnerSuccess, (state) => {
			return {
				...state,
				message: '',
				error: null,
				isLoading: false,
			};
		})
		.addCase(actions.submitFeedbackPartnerError, (state, { payload }) => {
			return {
				...state,
				error: payload,
				isLoading: false,
			};
		}),
);

export default reducer;
