import formatDateFns from 'date-fns/format';
import locale from 'date-fns/locale/ru';

type FormatParameters = Parameters<typeof formatDateFns>;

function formatDate(
	date: number | string | Date,
	format: FormatParameters[1],
	options?: FormatParameters[2],
): string {
	try {
		const result = formatDateFns(new Date(date), format, {
			locale,
			...options,
		});
		return result;
	} catch (error) {
		return '';
	}
}

export default formatDate;
