import { createReducer } from '@reduxjs/toolkit';
import actions from 'store/actions';
import { State } from 'types/state';

export const initialFeedbackClientModal: State['feedbackClientModal'] = {
	isOpen: false,
};

const reducer = createReducer(initialFeedbackClientModal, (builder) =>
	builder.addCase(actions.updateFeedbackClientModal, (state, { payload }) => {
		return {
			...state,
			...payload,
		};
	}),
);

export default reducer;
