<script lang="ts">import Modal from 'integration/lenta/modal/modal.svelte';
import OfferModalInfo from '../offerModalInfo.svelte';
import OfferModalCompany from '../OfferModalCompany.svelte';
import offerModalStore from 'integration/stores/offerModalStore';
import DisplayPromo from './displayPromo.svelte';
import InputPromo from './inputPromo.svelte';
$: modalState = $offerModalStore;
let promoHeight = 0;
let compressRef;
$: if (compressRef) {
    compressRef.style.height = `${promoHeight}px`;
}
$: promocodeState = modalState.offer?.promocode;
$: offer = modalState.offer;
$: similar = offer ? offer.similar : [];
</script>

<Modal isOpen={modalState.isOpen} onClose={offerModalStore.close}>
	{#if offer}
		<div class="container-1qezCg">
			<OfferModalInfo {offer} />
			<div class="company-3ICH5u">
				<OfferModalCompany company={offer.company} />
			</div>
			<div bind:this={compressRef} />
			{#if promocodeState?.payload}
				<DisplayPromo
					{similar}
					promocode={promocodeState.payload}
					bind:height={promoHeight}
				/>
			{:else}
				<InputPromo {offer} bind:height={promoHeight} />
			{/if}
		</div>
	{/if}
</Modal>

<style lang="scss">:global(.container-1qezCg) {
  height: 100vh;
  height: var(--app-height);
  overflow: auto;
  background-color: #fff;
  border: 1px solid #eeeeee;
}

:global(.company-3ICH5u) {
  border-top: 1px solid #eeeeee;
}</style>
