import { AppEmmiters, RootEmmiters } from './types';

type Emmiters = RootEmmiters;
type Listeners = AppEmmiters;

type EmmitersFieldTypeAsField = {
	[P in keyof Emmiters]: Emmiters[P] extends never ? never : P;
};

type EmmitersWithoutPayload = Omit<
	Emmiters,
	EmmitersFieldTypeAsField[keyof EmmitersFieldTypeAsField]
>;

type EmmitersWithPayload = Omit<Emmiters, keyof EmmitersWithoutPayload>;

interface EmitAction {
	type: keyof Emmiters;
	payload?: any;
}

interface ListenerAction {
	type: keyof Listeners;
	payload?: any;
}

class RootMessageEmitter {
	private listeners: Partial<
		Record<keyof Listeners, Array<(payload: any) => void>>
	> = {};

	constructor(public targetOrigin: string = '*', public target?: Window) {
		if (typeof window !== 'undefined') {
			window.addEventListener('message', this.messageListener);
		}
	}

	private postMessage = (message: EmitAction) => {
		// if (process.env.NODE_ENV === 'development') {
		// 	console.info(`RootMessageEmitter: [POST_MESSAGE]`);
		// 	console.info(message);
		// }
		if (this.target) {
			this.target.postMessage(message, this.targetOrigin);
		}
	};

	private messageListener = (e: MessageEvent<ListenerAction>) => {
		const { type, payload } = e.data;

		if (type === 'app:init' && this.targetOrigin === '*') {
			this.targetOrigin = (payload as Listeners['app:init']).origin;
		}

		// if (e.origin !== this.targetOrigin) {
		// 	return;
		// }

		// if (process.env.NODE_ENV === 'development') {
		// 	console.info(`RootMessageEmitter: [LISTENER_MESSAGE] `);
		// 	console.info(e.data);
		// }

		const actionListeners = this.listeners[type];
		if (actionListeners) {
			actionListeners.forEach((listener) => listener(payload));
		}
	};

	/**
	 * @param actionType - Тип события
	 * @param callback - Функция вызываемая при активация события
	 * @returns Функция отмены подписки
	 */
	addListener<K extends keyof Listeners>(
		actionType: K,
		callback: (payload: Listeners[K]) => void,
	) {
		let actionListeners = this.listeners[actionType];
		if (!actionListeners) {
			actionListeners = [];
		}
		actionListeners.push(callback);
		this.listeners[actionType] = actionListeners;
		return () => this.removeListener(actionType, callback);
	}

	/**
	 * @param actionType - Тип события
	 * @param callback - Функция, которая должны быть удалена из подписчиков
	 */
	removeListener<K extends keyof Listeners>(
		actionType: K,
		callback: (payload: Listeners[K]) => void,
	) {
		const actionListeners = this.listeners[actionType];
		if (actionListeners) {
			const index = actionListeners.indexOf(callback);
			actionListeners.splice(index, 1);
		}
	}

	/**
	 * @param actionType - Тип события
	 * @param callback - Функция вызываемая при активация события
	 */
	onOnce = <K extends keyof Listeners>(
		actionType: K,
		callback: (payload: Listeners[K]) => void,
	) => {
		let actionListeners = this.listeners[actionType];
		if (!actionListeners) {
			actionListeners = [];
		}
		const _callback = (payload: Listeners[K]) => {
			callback(payload);
			this.removeListener(actionType, _callback);
		};
		actionListeners.push(_callback);
		this.listeners[actionType] = actionListeners;
	};

	/**
	 * @param actionType - Тип события
	 * @param payload - Данные для отправки в
	 */
	emit<K extends keyof EmmitersWithoutPayload>(actionType: K): void;
	emit<K extends keyof EmmitersWithPayload>(
		actionType: K,
		payload: EmmitersWithPayload[K],
	): void;
	emit<K extends keyof Emmiters>(
		actionType: K,
		payload: Emmiters[K] | undefined = undefined,
	) {
		this.postMessage({
			type: actionType,
			payload: payload,
		});
	}
}

export default RootMessageEmitter;
