function responsiveContent(node: HTMLElement) {
	const step = 0.1;
	const minfs = 9;
	const scw = node.scrollWidth;
	const w = node.offsetWidth;
	if (scw > w) {
		let size = 'px';
		if (!node.style.fontSize) {
			const css = window.getComputedStyle(node);
			node.style.fontSize = css.fontSize;
			size = css.fontSize.slice(
				css.fontSize.length - 2,
				css.fontSize.length,
			);
		}
		const fontsize = parseInt(node.style.fontSize, 10) - step;
		if (fontsize >= minfs) {
			node.style.fontSize = `${fontsize}${size}`;
			responsiveContent(node);
		}
	}
}

export default responsiveContent;
