import { TransitionConfig } from 'svelte/transition';
import { cubicInOut } from 'svelte/easing';

function fadeScale(
	_node: Element,
	{ delay = 0, duration = 300, easing = cubicInOut, diff = 0.05 } = {},
): TransitionConfig {
	return {
		delay,
		duration,
		easing,
		css: (t) => `opacity: ${t}; transform: scale(${1 - diff + t * diff})`,
	};
}

export default fadeScale;
