import rootMessageEmitter from 'integration/rootMessageEmmiter';
import { initialClientState } from 'store/reducers/client';
import { writable } from 'svelte/store';
import type { ClientState } from 'types/state';
import type RootMessageEmitter from 'utils/messageEmitter/RootMessageEmitter';
import type { AppEmmiters } from 'utils/messageEmitter/types';

export function createClientStore(messageEmitter: RootMessageEmitter) {
	let isSync = false;
	const { subscribe, update } = writable<ClientState>(
		initialClientState,
		function start() {
			const onEmail = (payload: AppEmmiters['app:client/email']) => {
				update((prev) => ({
					...prev,
					email: payload,
				}));
			};

			const onSetState = (
				payload: AppEmmiters['app:client/setState'],
			) => {
				isSync = true;
				update(() => payload);
			};
			messageEmitter.addListener('app:client/email', onEmail);
			messageEmitter.addListener('app:client/setState', onSetState);
			messageEmitter.emit('root:client/getState');
			return function stop() {
				messageEmitter.removeListener('app:client/email', onEmail);
				messageEmitter.removeListener(
					'app:client/setState',
					onSetState,
				);
			};
		},
	);

	return {
		isSync: () => isSync,
		subscribe,
		setEmail: (email: string) => {
			messageEmitter.emit('root:client/email', email);
		},
	};
}

export default createClientStore(rootMessageEmitter);
