<script lang="ts">import eventStore from 'integration/stores/eventStore';
import { onDestroy } from 'svelte';
;
export let namespaceId;
let styleElement;
let eventState;
const unsubscribe = eventStore.subscribe((state) => {
    eventState = state;
});
$: if (eventState.event && namespaceId) {
    const styleText = eventState.event.style.replace(/(\..*?{)/g, (match) => {
        return `#${namespaceId} ${match}`;
    });
    if (styleElement) {
        styleElement.innerHTML = styleText;
    }
    else {
        styleElement = document.createElement('style');
        styleElement.setAttribute('id', namespaceId);
        styleElement.innerHTML = styleText;
        document.head.appendChild(styleElement);
    }
}
onDestroy(() => {
    unsubscribe();
    if (styleElement && styleElement.parentElement) {
        styleElement.parentElement.removeChild(styleElement);
    }
});
</script>
