<script lang="ts">import fadeScale from 'integration/transition/fadeScale';
import { onDestroy, onMount } from 'svelte';
import NavMenuItem from './navMenuItem.svelte';
import navStore from 'integration/stores/navStore';
import getFrameApp from 'integration/getFrameApp';
$: navState = $navStore;
$: isExpand = navState.isExpand;
$: expandRect = navState.expandRect;
let menuStyle = '';
let menuArrowStyle = '';
function onResize() {
    const iframeRect = getFrameApp().getBoundingClientRect();
    menuStyle = `
			top: calc(${iframeRect.top +
        expandRect.top +
        expandRect.height +
        window.pageYOffset}px + 1rem); 
			left: ${iframeRect.left}px;
			width: ${iframeRect.width}px;
		`;
    menuArrowStyle = `transform: translate(calc(${expandRect.left + expandRect.width / 2}px - 50%),0)`;
    if (isExpand) {
        if (window.screen.width < 768) {
            document.body.style.overflow = 'hidden';
        }
        else {
            document.body.style.overflow = '';
        }
    }
}
$: if (isExpand) {
    onResize();
}
else {
    document.body.style.overflow = '';
}
function handleActiveKey(event) {
    navStore.setActiveKey(event.detail);
}
let containerRef;
function handleClose(e) {
    function isExcluded() {
        let parent = e.target;
        while (parent) {
            if (parent === containerRef) {
                return true;
            }
            parent = parent.parentNode;
        }
        return false;
    }
    if (navState.isExpand) {
        if (!isExcluded()) {
            navStore.closeMenu();
        }
    }
}
onMount(() => {
    getFrameApp().addEventListener('click', handleClose);
    window.addEventListener('click', handleClose);
    window.addEventListener('resize', onResize);
    onResize();
});
onDestroy(() => {
    getFrameApp().removeEventListener('click', handleClose);
    window.removeEventListener('click', handleClose);
    window.removeEventListener('resize', onResize);
    document.body.style.overflow = '';
});
</script>

{#if navState.isExpand}
	<div
		transition:fadeScale={{ duration: 100 }}
		class={'menu-2eITeB'}
		style={menuStyle}
		bind:this={containerRef}
	>
		<div class="arrow-1Ub3GV" style={menuArrowStyle} />
		<div class="header-V8Rt7l">
			<div class="title--utsYD">Скидки и акции</div>
			<div class="close-31FWZZ" on:click={navStore.closeMenu} />
		</div>
		<div class="content-2yh7j_">
			<div class="body-1GtB5x">
				{#each navState.categories as { name, eventKey, image }}
					<NavMenuItem
						{name}
						{image}
						{eventKey}
						active={navState.activeKey === eventKey}
						on:activeKey={handleActiveKey}
					/>
				{/each}
			</div>
		</div>
	</div>
{/if}

<style lang="scss">:global(.menu-2eITeB) {
  box-sizing: border-box;
  transform-origin: top right;
  position: absolute;
  border-radius: 1rem;
  border: 1px solid #eeeeee;
  box-shadow: 0px 26px 21px -22px rgba(0, 0, 0, 0.2), 0px 20px 33px rgba(0, 0, 0, 0.05);
  background: #ffffff;
  z-index: 101;
  max-height: 100vh;
  max-height: var(--app-height);
  display: flex;
  flex-direction: column;
}

:global(.content-2yh7j_) {
  overflow: auto;
}

:global(.header-V8Rt7l) {
  padding: 1rem;
  display: none;
}

:global(.title--utsYD) {
  font-weight: 500;
  color: #000000;
}

:global(.close-31FWZZ) {
  box-sizing: content-box;
  width: 1em;
  height: 1em;
  padding: 0.25em 0.25em;
  color: #000;
  background: transparent url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23000'%3e%3cpath d='M.293.293a1 1 0 011.414 0L8 6.586 14.293.293a1 1 0 111.414 1.414L9.414 8l6.293 6.293a1 1 0 01-1.414 1.414L8 9.414l-6.293 6.293a1 1 0 01-1.414-1.414L6.586 8 .293 1.707a1 1 0 010-1.414z'/%3e%3c/svg%3e") center/1em auto no-repeat;
  border: 0;
  border-radius: 0.25rem;
}

:global(.body-1GtB5x) {
  padding: 40px;
  display: flex;
  flex-wrap: wrap;
}

:global(.arrow-1Ub3GV) {
  position: absolute;
  top: calc(-0.5rem - 1px);
  left: 0;
  display: block;
  width: 1rem;
  height: 0.5rem;
}
:global(.arrow-1Ub3GV::before) {
  position: absolute;
  display: block;
  content: "";
  border-color: transparent;
  border-style: solid;
  top: 0;
  border-width: 0 0.5rem 0.5rem 0.5rem;
  border-bottom-color: rgba(0, 0, 0, 0.25);
}
:global(.arrow-1Ub3GV::after) {
  position: absolute;
  display: block;
  content: "";
  border-color: transparent;
  border-style: solid;
  top: 1px;
  border-width: 0 0.5rem 0.5rem 0.5rem;
  border-bottom-color: #fff;
}

@media (max-width: 767px) {
  :global(.menu-2eITeB) {
    position: fixed !important;
    top: 0 !important;
    left: 0 !important;
    width: 100vw !important;
    width: var(--app-width) !important;
    height: 100vh !important;
    height: var(--app-height) !important;
    border-radius: 0 !important;
  }

  :global(.header-V8Rt7l) {
    display: flex;
    flex-shrink: 0;
    align-items: center;
    justify-content: space-between;
  }

  :global(.body-1GtB5x) {
    padding: 30px;
  }
}</style>
