<script lang="ts">import { onDestroy, onMount } from 'svelte';
import DesktopModal from './desktopModal/desktopModal.svelte';
import MobileModal from './mobileModal/mobileModal.svelte';
let isMobile = window.screen.width <= 768;
function resize() {
    isMobile = window.screen.width <= 768;
}
onMount(() => {
    window.addEventListener('resize', resize);
});
onDestroy(() => {
    window.removeEventListener('resize', resize);
});
</script>

{#if isMobile}
	<MobileModal />
{:else}
	<DesktopModal />
{/if}
