import { createReducer } from '@reduxjs/toolkit';
import actions from 'store/actions';
import { State } from 'types/state';

export const initialClientState: State['client'] = {
	clientGA: '',
	clientID: '',
	email: '',
};

const reducer = createReducer(initialClientState, (builder) =>
	builder.addCase(actions.updateClientState, (state, { payload }) => {
		return {
			...state,
			...payload,
		};
	}),
);

export default reducer;
