<script lang="ts">import Responsive from 'integration/common/responsive/responsive.svelte';
import responsiveContent from 'integration/utils/responsiveContent';
import { PromocodeType } from 'types/enums';
import OfferModalSimilar from '../offerModalSimilar.svelte';
;
;
export let promocode;
export let similar;
export let height;
let containerRef;
let contentRef;
let titleRef;
let scrollRef;
$: if (titleRef && containerRef) {
    const inputRect = titleRef.getBoundingClientRect();
    const containerRect = containerRef.getBoundingClientRect();
    height =
        containerRef.clientHeight -
            (containerRect.bottom - inputRect.bottom);
    containerRef.style.height = `${height}px`;
}
let isExpand = !!similar.length;
$: if (similar.length) {
    isExpand = true;
}
else {
    isExpand = false;
}
$: if (isExpand && scrollRef) {
    scrollRef.scrollTo(0, 0);
}
$: if (containerRef && contentRef) {
    if (isExpand) {
        containerRef.style.height = `${window.innerHeight}px`;
    }
    else {
        containerRef.style.height = `${height}px`;
    }
}
$: if (scrollRef) {
    if (isExpand) {
        scrollRef.style.overflow = 'auto';
    }
    else {
        scrollRef.style.overflow = 'visible';
    }
}
function toggleExpand() {
    isExpand = !isExpand;
}
function onExpand(is) {
    isExpand = is;
}
let positionField = 'screenY';
let lastPosition = 0;
const onTouchstart = (e) => {
    const currentPosition = e.changedTouches[0][positionField];
    lastPosition = currentPosition;
};
const onTouchend = (e) => {
    if (scrollRef.scrollTop === 0) {
        const currentPosition = e.changedTouches[0][positionField];
        const delta = lastPosition - currentPosition;
        lastPosition = currentPosition;
        if (delta > 1) {
            onExpand(true);
        }
        if (delta < -1) {
            onExpand(false);
        }
    }
};
</script>

<div
	class="container--iD7lo"
	bind:this={containerRef}
	on:touchstart={onTouchstart}
	on:touchend={onTouchend}
>
	<div class="content-3ounC7" bind:this={contentRef}>
		<div class="line-3O3X64" />
		<div
			class="arrow-118MYq"
			class:down-Wc4PQ5={isExpand}
			class:up-w60G5f={!isExpand}
			on:click={toggleExpand}
		/>
		<div class="content__scroll-13pw6T" bind:this={scrollRef}>
			<div class="title-MI_VUm" bind:this={titleRef}>Ваш промокод</div>
			{#if promocode.type === PromocodeType.Default}
				<div class="desc-VUcqmw">
					Покажите его кассиру или введите номер при оплате на сайте
				</div>
				<div class="card-26LxX1 card_text">
					<div class="promo-text-3PTa4u" use:responsiveContent>
						{promocode.promocode}
					</div>
				</div>
			{:else if promocode.type === PromocodeType.QR_link || promocode.type === PromocodeType.QR_raw}
				<div class="desc-VUcqmw">Покажите его кассиру при оплате</div>
				<div class="card-26LxX1 card_qr">
					<Responsive size="1x1">
						<img
							alt={promocode.promocode}
							src={promocode.promocode}
						/>
					</Responsive>
				</div>
			{:else if promocode.type === PromocodeType.CODE128 || promocode.type === PromocodeType.EAN13}
				<div class="desc-VUcqmw">
					Покажите его кассиру или введите номер при оплате на сайте
				</div>
				<div class="card-26LxX1">
					<Responsive size="2x1">
						<img
							alt={promocode.promocode}
							src={promocode.promocode}
						/>
					</Responsive>
				</div>
			{/if}
			<div class="desc-VUcqmw">Промокод отправлен на ваш email</div>
			<div class="divider-1KQvnr" />
			{#if similar.length}
				<OfferModalSimilar offers={similar} />
			{/if}
		</div>
	</div>
</div>

<style lang="scss">:global(.container--iD7lo) {
  max-height: calc(100vh - 60px);
  max-height: calc(var(--app-height) - 60px);
  width: 100%;
  position: fixed;
  bottom: 0;
  z-index: 100;
  height: auto;
  transition: position 0.3s, height 0.3s;
}

:global(.content-3ounC7) {
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #f8f8f8;
  padding-top: 30px;
  text-align: center;
  justify-content: center;
  box-shadow: 0px -10px 9px -10px rgba(0, 0, 0, 0.1), 0px -2px 39px rgba(0, 0, 0, 0.2);
  border-radius: 16px 16px 0px 0px;
}

:global(.content__scroll-13pw6T) {
  padding: 0 20px 30px 20px;
  overflow: auto;
  height: calc(100vh - 60px);
  height: calc(var(--app-height) - 60px);
}

:global(.line-3O3X64) {
  position: absolute;
  background: #e0e0e0;
  border-radius: 50px;
  height: 4px;
  width: 64px;
  top: 10px;
  right: 50%;
  transform: translate(50%, 0);
}

:global(.arrow-118MYq) {
  top: 15px;
  right: 15px;
  position: absolute;
  border: solid #bdbdbd;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
  transform: translate(0, -100%);
}

:global(.up-w60G5f) {
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
}

:global(.down-Wc4PQ5) {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

:global(.title-MI_VUm) {
  font-weight: 700;
  font-size: 1.15em;
  padding-bottom: 0.5em;
}

:global(.desc-VUcqmw) {
  font-weight: 400;
  font-size: 0.875em;
  margin-bottom: 1em;
  color: #979797;
}

:global(.card-26LxX1) {
  background: #ffffff;
  border: 1px solid #eeeeee;
  box-shadow: 0px 13px 18px -14px rgba(0, 0, 0, 0.2);
  border-radius: 0.5em;
  padding: 20px;
  margin-bottom: 0.5em;
  font-weight: 900;
  font-size: 2em;
  vertical-align: middle;
}
:global(.card-26LxX1) img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.card_qr {
  margin-left: 40px;
  margin-right: 40px;
}

.card_text {
  margin-left: auto;
  margin-right: auto;
}

:global(.promo-text-3PTa4u) {
  padding: 20px 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  margin-left: -15px;
  margin-right: -15px;
}

:global(.divider-1KQvnr) {
  height: 1px;
  border-top: 1px solid #e0e0e0;
}</style>
