import rootMessageEmitter from 'integration/rootMessageEmmiter';
import { initialFeedbackClientModal } from 'store/reducers/feedbackClientModal';
import { writable } from 'svelte/store';
import type { FeedbackClientModalState } from 'types/state';
import type RootMessageEmitter from 'utils/messageEmitter/RootMessageEmitter';
import type { AppEmmiters } from 'utils/messageEmitter/types';

export function createFeedbackClientModalStore(
	messageEmitter: RootMessageEmitter,
) {
	let isSync = false;
	const { subscribe, update } = writable<FeedbackClientModalState>(
		initialFeedbackClientModal,
		function start() {
			const onSetState = (
				payload: AppEmmiters['app:feedback-client-modal/setState'],
			) => {
				isSync = true;
				update(() => payload);
			};
			const onUpdate = (
				payload: AppEmmiters['app:feedback-client-modal/update'],
			) => {
				update((prev) => ({ ...prev, ...payload }));
			};
			messageEmitter.addListener(
				'app:feedback-client-modal/setState',
				onSetState,
			);
			messageEmitter.addListener(
				'app:feedback-client-modal/update',
				onUpdate,
			);
			messageEmitter.emit('root:feedback-client-modal/getState');
			return function stop() {
				messageEmitter.removeListener(
					'app:feedback-client-modal/setState',
					onSetState,
				);
				messageEmitter.removeListener(
					'app:feedback-client-modal/update',
					onUpdate,
				);
			};
		},
	);

	return {
		isSync: () => isSync,
		subscribe,
		open: () => {
			messageEmitter.emit('root:feedback-client-modal/update', {
				isOpen: true,
			});
		},
		close: () => {
			messageEmitter.emit('root:feedback-client-modal/update', {
				isOpen: false,
			});
		},
	};
}

export default createFeedbackClientModalStore(rootMessageEmitter);
