<script>
	export let size = 60;
	export let color = '#FF3E00';
	export let unit = 'px';
	export let duration = '0.6s';
	let moonSize = size / 7;
	let top = size / 2 - moonSize / 2;
</script>

<div
	class="wrapper-139Mmg"
	style="--size: {size}{unit}; --color: {color}; --moonSize: {top}{unit}; --duration: {duration};"
>
	<div class="circle-one-35Ln06" />
	<div class="circle-two-O91xjH" />
</div>

<style>
	:global(.wrapper-139Mmg) {
		height: var(--size);
		width: var(--size);
		border-radius: 100%;
		animation: moonStretchDelay var(--duration) 0s infinite linear;
		animation-fill-mode: forwards;
		position: relative;
	}
	:global(.circle-one-35Ln06) {
		top: var(--moonSize);
		background-color: var(--color);
		width: calc(var(--size) / 7);
		height: calc(var(--size) / 7);
		border-radius: 100%;
		animation: moonStretchDelay var(--duration) 0s infinite linear;
		animation-fill-mode: forwards;
		opacity: 0.8;
		position: absolute;
	}
	:global(.circle-two-O91xjH) {
		opacity: 0.1;
		border: calc(var(--size) / 7) solid var(--color);
		height: var(--size);
		width: var(--size);
		border-radius: 100%;
		box-sizing: border-box;
	}
	@keyframes moonStretchDelay {
		100% {
			transform: rotate(360deg);
		}
	}
</style>
