import { createAction } from '@reduxjs/toolkit';
import {
	ClientState,
	ComboModalState,
	FeedbackClientModalState,
	FeedbackPartnerModalState,
	OfferModalState,
} from 'types/state';
import { ErrorBody } from 'utils/request';
import { v4 } from 'uuid';
import {
	PayloadView,
	PayloadActivate,
	PayloadActivateSuccess,
	ErrorPayload,
	PayloadViewSuccess,
	PayloadSubmitFeedbackPartner,
	PayloadSimilar,
	PayloadSimilarSuccess,
	PayloadComboView,
} from './types';

const actions = {
	view: createAction('view', (payload: PayloadView) => {
		return {
			payload,
			meta: {
				requestID: v4(),
			},
		};
	}),
	viewCombo: createAction('viewCombo', (payload: PayloadComboView) => {
		return {
			payload,
			meta: {
				requestID: v4(),
			},
		};
	}),
	viewSuccess: createAction<PayloadViewSuccess>('view/success'),
	viewError: createAction<ErrorPayload<PayloadView>>('view/error'),
	getSimilar: createAction('similar', (payload: PayloadSimilar) => {
		return {
			payload,
			meta: {
				requestID: v4(),
			},
		};
	}),
	getSimilarSuccess: createAction<PayloadSimilarSuccess>('similar/success'),
	getSimilarError: createAction<ErrorPayload<PayloadSimilar>>(
		'similar/error',
	),
	activate: createAction('activate', (payload: PayloadActivate) => {
		return {
			payload,
			meta: {
				requestID: v4(),
			},
		};
	}),
	activateSuccess: createAction<PayloadActivateSuccess>('activate/success'),
	activateError: createAction<ErrorPayload<PayloadActivate>>(
		'activate/error',
	),
	updateOfferModalState: createAction<Partial<OfferModalState>>(
		'offerModal/update',
	),
	updateComboModalState: createAction<Partial<ComboModalState>>(
		'comboModal/update',
	),
	updateClientState: createAction<Partial<ClientState>>('client/update'),
	updateFeedbackPartnerModal: createAction<
		Partial<FeedbackPartnerModalState>
	>('feedbackPartner/update'),
	updateFeedbackClientModal: createAction<Partial<FeedbackClientModalState>>(
		'feedbackClient/update',
	),
	submitFeedbackPartner: createAction<PayloadSubmitFeedbackPartner>(
		'feedbackPartner/submit',
	),
	submitFeedbackPartnerSuccess: createAction(
		'feedbackPartner/submit/success',
	),
	submitFeedbackPartnerError: createAction<ErrorBody>(
		'feedbackPartner/submit/error',
	),
};
export default actions;
