import 'core-js';
import 'regenerator-runtime/runtime';
import querystring from 'querystring';
import messageEmitter from './rootMessageEmmiter';
import getFrameApp, { updateFrameApp } from './getFrameApp';
import getClientID from './getClientID';
import { AppEmmiters } from 'utils/messageEmitter/types';
import tmt from './tmt';
import throttle from 'lodash/throttle';
import getClientGA from './getClientGA';

type InitFunction = () => void;

export default async function init(initFunction: InitFunction) {
	messageEmitter.addListener('app:init', () => {
		const iframe = getFrameApp();
		if (iframe && iframe.contentWindow) {
			iframe.style.background = '';
			messageEmitter.target = iframe.contentWindow;
			tmt.init();
		}
	});

	messageEmitter.addListener('app:height', (payload) => {
		const iframe = getFrameApp();
		iframe.style.height = payload.height + 'px';
	});

	messageEmitter.addListener('app:onClick', () => {
		const iframe = getFrameApp();
		iframe.click();
	});

	messageEmitter.addListener(
		'app:scrollTop',
		(payload: AppEmmiters['app:scrollTop']) => {
			// const iframe = getFrameApp();
			window.scrollTo({
				behavior: payload.behavior,
				top: 0,
				// top: iframe.offsetTop,
			});
			// iframe.scrollIntoView({ block: 'start', behavior: 'smooth' });
		},
	);

	let prevScrollTop = 0;

	const documentElement = window.document.documentElement;
	const scrollHandler = throttle(function scrollHandler() {
		const st = window.pageYOffset;
		messageEmitter.emit('root:scroll', {
			clientHeight: documentElement.clientHeight,
			scrollHeight: documentElement.scrollHeight,
			scrollTop: window.pageYOffset,
			prevScrollTop,
			upScroll: st < prevScrollTop,
			downScroll: st > prevScrollTop,
		});
		prevScrollTop = st <= 0 ? 0 : st;
	}, 300);

	function resizeHandler() {
		messageEmitter.emit('root:resize');
	}
	window.addEventListener('scroll', scrollHandler);
	window.addEventListener('resize', resizeHandler);

	// window.removeEventListener('scroll', scrollHandler);
	// window.removeEventListener('resize', resizeHandler);

	messageEmitter.onOnce('app:init', initFunction);

	const clientGA = await getClientGA();

	async function start() {
		update();
		const observer = new MutationObserver(() => {
			update();
		});

		observer.observe(document, {
			// subtree: true,
			childList: true,
		});
	}

	function update() {
		try {
			updateFrameApp();
			const iframe = getFrameApp();
			if (!iframe.src) {
				const params: Record<string, any> = {
					origin: window.origin,
				};
				const clientID = getClientID();
				if (clientID) {
					params.clientID = clientID;
				}
				if (clientGA) {
					params.clientGA = clientGA;
				}
				const queryParams = querystring.stringify(params);
				let src = iframe.dataset.source;
				if (src) {
					const indexSearch = src.indexOf('?');
					if (indexSearch === -1) {
						src = src + '?' + queryParams;
					} else {
						src = src + '&' + queryParams;
					}
					let search = window.location.search;
					if (search) {
						search = search.slice(1);
						src = src + '&' + search;
					}
					iframe.src = src;
				}
			}
		} catch (error) {
			if (process.env.NODE_ENV === 'development') {
				console.error(error);
			}
		}
	}

	if (
		document.readyState === 'loading' ||
		document.readyState === 'interactive'
	) {
		document.onreadystatechange = function () {
			if (document.readyState === 'complete') {
				start();
			}
		};
	} else {
		start();
	}
}
