import { getCookie } from 'utils/getCookie';

export default async function getClientGA() {
	try {
		return await new Promise<string>((resolve, reject) => {
			if (window.dataLayer && typeof window.ga !== 'function') {
				window.ga = function () {
					// eslint-disable-next-line prefer-rest-params
					(window.ga.q = window.ga.q || []).push(arguments);
				} as any;
				window.ga.l = +new Date();
			}
			if (typeof window.ga === 'function') {
				// eslint-disable-next-line @typescript-eslint/no-unused-vars
				window.ga((_tracker) => {
					// получение clientGA средствами скрипта аналитики (без GA.x.x.)
					// if (!_tracker) {
					// 	_tracker = window.ga.getAll()[0];
					// }
					// const clientGA = _tracker.get('clientId');
					const clientGA = getCookie('_ga');
					if (clientGA) {
						resolve(clientGA);
					} else {
						console.warn('clientGA named _ga not found');
						reject();
					}
				});
			} else {
				console.warn('Google analytics not defined');
				reject();
			}
		});
	} catch (error) {
		return undefined;
	}
}
